.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr ;
    gap: 2rem;
}

.experience__container >div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container>div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: #5f5fa5;
}
.experience__container>div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: #5f5fa5;
}

.experience__content{
    display: grid;
    width:100%;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;

}

.experience__details{
    display: grid;
    margin:auto;
    gap: 1rem;
}

.experience__details-icons{
    margin-top: 6px;
    color: #5f5fa5;
}

/* Media quries (Medium devices)*/
@media screen and (max-width:1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container>div{
        width: 80%;
        padding: 2rem;
        margin: auto;
    }
    .experience__content{
        padding: 1rem;
    }

}

/* Media quries (small devices)*/

@media screen and (max-width:600px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container{
        gap: 1rem;
    }
    .experience__container>div{
        width: 100%;
        padding: 2rem 1rem;
    }
    .experience__content{
        padding: 1rem;
    }
}
